.clearfix:after {
	display: table;
	clear: both;
	content: '';
}
.d-none,
[hidden] {
	display: none !important;
}
.d-flex {
	display: flex;
}
.fw-300 {
	font-weight: 300;
}
.fw-400 {
	font-weight: 400;
}
.fw-500 {
	font-weight: 500;
}
.fw-600 {
	font-weight: 600;
}
.fw-700 {
	font-weight: 700;
}
.fw-800 {
	font-weight: 800;
}
.fw-900 {
	font-weight: 900;
}
.text-center {
	text-align: center;
}
.text-left {
	text-align: left;
}
.text-right {
	text-align: right;
}
.text-justify {
	text-align: justify;
}
.text-last-center {
	text-align-last: center;
}
.text-underline {
	text-decoration: underline;
}
.text-no-underline {
	text-decoration: none;
}
.text-uppercase {
	text-transform: uppercase;
}
.text-capitalize {
	text-transform: capitalize;
}
.justify-content-center {
	justify-content: center;
}
.justify-content-end {
	justify-content: flex-end;
}
.justify-content-start {
	justify-content: flex-start;
}
.justify-content-between {
	justify-content: space-between;
}
.align-items-center {
	align-items: center;
}
.align-items-end {
	align-items: flex-end;
}
.align-items-start {
	align-items: flex-start;
}
.fullsize {
	width: 100%;
	height: 100%;
}
.h-100 {
	height: 100%;
}
.padding-left-0 {
	padding-left: 0 !important;
}
.padding-right-0 {
	padding-right: 0 !important;
}
.padding-x-0 {
	@extend .padding-left-0;
	@extend .padding-right-0;
}
.object-resize {
	position: absolute;
	top: 0;
	left: 0;
}
.object-cover {
	@extend .fullsize;
	object-fit: cover;
}
.overflow-hidden {
	overflow: hidden;
}
.object-contain {
	@extend .fullsize;
	object-fit: contain;
	position: absolute;
	top: 0;
	left: 0;
}
.pt-section {
	padding-top: 5rem;
	@media (min-width: 1200px) {
		padding-top: 8rem;
	}
}
.pb-section {
	padding-bottom: 5rem;
	@media (min-width: 1200px) {
		padding-bottom: 8rem;
	}
}
.pd-section {
	@extend .pt-section;
	@extend .pb-section;
}
.mt-section {
	margin-top: 5rem;
	@media (min-width: 1200px) {
		margin-top: 8rem;
	}
}
.mb-section {
	margin-bottom: 5rem;
	@media (min-width: 1200px) {
		margin-bottom: 8rem;
	}
}
.mr-section {
	@extend .mt-section;
	@extend .mb-section;
}
.mt-title {
	margin-top: 2.4rem;
	@media (max-width: 575.98px) {
		margin-top: 1.6rem;
	}
}
.mb-title {
	margin-bottom: 2.4rem;
	@media (max-width: 575.98px) {
		margin-bottom: 1.6rem;
	}
}
.mr-title {
	@extend .mt-section;
	@extend .mb-section;
}

.mt-24 {
	margin-top: 24px;
}

.text-truncate {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.cl-white {
	color: $hvh-neutral-white-color;
}

.cl-success {
	color: $hvh-semantic-positive-color;
}

.cl-information {
	color: $hvh-semantic-information-color;
}

.cl-danger {
	color: $hvh-semantic-negative-color;
}

.bg-primary-blue-light-5 {
	background-color: $hvh-primary-blue-light-color-05;
}

.border-primary-blue-light-color-05 {
	border: 1px solid $hvh-primary-blue-light-color-05;
}

.bg-primary {
	background-color: $hvh-primary-color;
}

.bg-information {
	background-color: $hvh-semantic-information-color;
}

.bg-information-lightest {
	background-color: $hvh-semantic-information-lightest-color;
}

.bg-success {
	background-color: $hvh-semantic-positive-color;
}

.bg-success-lightest {
	background-color: $hvh-semantic-positive-lightest-color;
}

.bg-danger {
	background-color: $hvh-semantic-negative-color;
}

.bg-danger-lightest {
	background-color: $hvh-semantic-negative-lightest-color;
}

.bg-warning {
	background-color: $hvh-semantic-critical-color;
}

.border-primary {
	border: 1px solid $hvh-primary-color;
}

.pos-center-center {
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}
